"use client";

import { useState, useEffect } from "react";
import { useParams, useRouter } from "next/navigation";
import dynamic from "next/dynamic";
import { fetchAPI } from "@/lib/api";
import { toast } from "sonner";
import { ArrowLeft } from "lucide-react";
import Link from "next/link";

const Editor = dynamic(() => import("@/components/Editor"), {
    ssr: false,
    loading: () => <div className="h-[300px] w-full bg-slate-50 border border-slate-200 rounded-xl animate-pulse" />
});

export default function EditProject() {
    const params = useParams();
    const router = useRouter();
    const id = params.id as string;

    const [title, setTitle] = useState("");
    const [slug, setSlug] = useState("");
    const [description, setDescription] = useState("");
    const [content, setContent] = useState("");
    const [role, setRole] = useState("");
    const [tags, setTags] = useState("");
    const [demoLink, setDemoLink] = useState("");
    const [githubLink, setGithubLink] = useState("");
    const [coverImage, setCoverImage] = useState("");
    const [loading, setLoading] = useState(false);
    const [fetching, setFetching] = useState(true);

    useEffect(() => {
        fetchAPI(`/projects/${id}`)
            .then((project) => {
                setTitle(project.title || "");
                setSlug(project.slug || "");
                setDescription(project.description || "");
                setContent(project.content || "");
                setRole(project.role || "");
                setTags(Array.isArray(project.tags) ? project.tags.join(", ") : "");
                setDemoLink(project.demo_link || project.demoUrl || "");
                setGithubLink(project.github_link || project.repoUrl || "");
                setCoverImage(project.cover_image || project.coverImage || "");
                setFetching(false);
            })
            .catch((err) => {
                console.error(err);
                toast.error("Failed to load project");
                setFetching(false);
            });
    }, [id]);

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setLoading(true);

        try {
            await fetchAPI(`/projects/${id}`, {
                method: 'PUT',
                body: JSON.stringify({
                    title,
                    slug,
                    description,
                    content,
                    role,
                    tags: tags.split(',').map(t => t.trim()).filter(Boolean),
                    demo_link: demoLink,
                    github_link: githubLink,
                    cover_image: coverImage,
                }),
            });
            toast.success('Project updated successfully!');
            setTimeout(() => router.push('/admin/projects'), 1500);
        } catch (error) {
            console.error('Failed to update project', error);
            toast.error('Failed to update project');
        } finally {
            setLoading(false);
        }
    };

    if (fetching) {
        return (
            <div className="max-w-4xl mx-auto">
                <div className="h-8 w-48 bg-slate-200 rounded animate-pulse mb-8" />
                <div className="space-y-6">
                    <div className="h-12 bg-slate-200 rounded animate-pulse" />
                    <div className="h-12 bg-slate-200 rounded animate-pulse" />
                    <div className="h-[300px] bg-slate-200 rounded animate-pulse" />
                </div>
            </div>
        );
    }

    return (
        <div className="max-w-4xl mx-auto pb-10">
            <div className="flex items-center gap-4 mb-8">
                <Link href="/admin/projects" className="p-2 hover:bg-slate-100 rounded-lg transition-colors">
                    <ArrowLeft size={20} className="text-slate-400" />
                </Link>
                <h1 className="text-3xl font-black text-[#0d141b]">Edit Project</h1>
            </div>

            <form onSubmit={handleSubmit} className="space-y-6">
                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                    <div>
                        <label className="block text-sm font-bold uppercase tracking-wide text-slate-500 mb-2">Project Title</label>
                        <input
                            type="text"
                            value={title}
                            onChange={(e) => setTitle(e.target.value)}
                            className="w-full px-4 py-3 bg-white border border-slate-200 rounded-lg focus:outline-none focus:border-[#2b8cee] font-bold text-lg text-[#0d141b]"
                            placeholder="e.g. Finance Dashboard"
                            required
                        />
                    </div>
                    <div>
                        <label className="block text-sm font-bold uppercase tracking-wide text-slate-500 mb-2">Slug</label>
                        <input
                            type="text"
                            value={slug}
                            onChange={(e) => setSlug(e.target.value)}
                            className="w-full px-4 py-3 bg-slate-50 border border-slate-200 rounded-lg focus:outline-none focus:border-[#2b8cee] font-mono text-sm text-slate-600"
                            placeholder="finance-dashboard"
                            required
                        />
                    </div>
                </div>

                <div>
                    <label className="block text-sm font-bold uppercase tracking-wide text-slate-500 mb-2">Short Description</label>
                    <textarea
                        value={description}
                        onChange={(e) => setDescription(e.target.value)}
                        className="w-full px-4 py-3 bg-white border border-slate-200 rounded-lg focus:outline-none focus:border-[#2b8cee] min-h-[80px] text-[#0d141b]"
                        placeholder="Brief overview of the project..."
                        required
                    />
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                    <div>
                        <label className="block text-sm font-bold uppercase tracking-wide text-slate-500 mb-2">Role</label>
                        <input
                            type="text"
                            value={role}
                            onChange={(e) => setRole(e.target.value)}
                            className="w-full px-4 py-3 bg-white border border-slate-200 rounded-lg focus:outline-none focus:border-[#2b8cee] text-[#0d141b]"
                            placeholder="e.g. Lead Designer"
                        />
                    </div>
                    <div>
                        <label className="block text-sm font-bold uppercase tracking-wide text-slate-500 mb-2">Tags (comma separated)</label>
                        <input
                            type="text"
                            value={tags}
                            onChange={(e) => setTags(e.target.value)}
                            className="w-full px-4 py-3 bg-white border border-slate-200 rounded-lg focus:outline-none focus:border-[#2b8cee] text-[#0d141b]"
                            placeholder="Next.js, Tailwind, Supabase"
                        />
                    </div>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                    <div>
                        <label className="block text-sm font-bold uppercase tracking-wide text-slate-500 mb-2">Demo Link</label>
                        <input
                            type="url"
                            value={demoLink}
                            onChange={(e) => setDemoLink(e.target.value)}
                            className="w-full px-4 py-3 bg-white border border-slate-200 rounded-lg focus:outline-none focus:border-[#2b8cee] text-[#0d141b]"
                            placeholder="https://..."
                        />
                    </div>
                    <div>
                        <label className="block text-sm font-bold uppercase tracking-wide text-slate-500 mb-2">GitHub Link</label>
                        <input
                            type="url"
                            value={githubLink}
                            onChange={(e) => setGithubLink(e.target.value)}
                            className="w-full px-4 py-3 bg-white border border-slate-200 rounded-lg focus:outline-none focus:border-[#2b8cee] text-[#0d141b]"
                            placeholder="https://github.com/..."
                        />
                    </div>
                </div>

                <div>
                    <label className="block text-sm font-bold uppercase tracking-wide text-slate-500 mb-2">Cover Image URL</label>
                    <input
                        type="text"
                        value={coverImage}
                        onChange={(e) => setCoverImage(e.target.value)}
                        className="w-full px-4 py-3 bg-white border border-slate-200 rounded-lg focus:outline-none focus:border-[#2b8cee] text-[#0d141b]"
                        placeholder="https://..."
                    />
                </div>

                <div>
                    <label className="block text-sm font-bold uppercase tracking-wide text-slate-500 mb-2">Case Study Content</label>
                    <Editor content={content} onChange={setContent} />
                </div>

                <div className="flex justify-end gap-4 pt-6">
                    <Link
                        href="/admin/projects"
                        className="px-6 py-3 border border-slate-200 text-slate-500 font-bold rounded-lg hover:bg-slate-50 transition-colors"
                    >
                        Cancel
                    </Link>
                    <button
                        type="submit"
                        disabled={loading}
                        className="px-8 py-4 bg-[#2b8cee] text-white font-bold rounded-lg hover:bg-[#2b8cee]/90 transition-colors disabled:opacity-50"
                    >
                        {loading ? 'Saving...' : 'Save Changes'}
                    </button>
                </div>
            </form>
        </div>
    );
}

